<template>
    <div class="pos">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">


          <el-form-item label="姓名" prop="name">
            <el-col :span="24">  <el-input v-model="ruleForm.name" placeholder="不能为空" ></el-input></el-col>
          </el-form-item>

         <el-form-item label="性别" prop="gender">
         <el-radio v-model="ruleForm.gender" label=男>男</el-radio>
         <el-radio v-model="ruleForm.gender" label=女>女</el-radio>
         </el-form-item>

           <el-form-item label="手机号码" prop="phoneNum" >
            <el-col :span="24"> <el-input  v-model="ruleForm.phoneNum" placeholder="长度为11位，满足号码规则"></el-input></el-col>
          </el-form-item>

         <el-form-item label="密码" prop="password">
            <el-col :span="24"><el-input type="password" v-model="ruleForm.password" autocomplete="off" show-password placeholder="长度为8-16位"></el-input></el-col>
          </el-form-item>

          <el-form-item label="确认密码" prop="checkPass">
            <el-col :span="24"><el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" show-password></el-input></el-col>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary"  round @click="submitForm('ruleForm')">立即修改</el-button>
          </el-form-item>
    </el-form>
  </div>
</template>
<style scoped>
    .red{color: red;font-size: 16px;}
    .pos{position: absolute;left:40%;top:20%}
    p{font-size: 20px;}
    span{cursor:pointer;}
    span:hover{color:#41b883;}
    select{font-size: 16px;display:block; width:80px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; margin-right: 175px;outline:none; border:1px solid #888;  box-sizing:border-box;}
</style>

<script scoped>
import {
  addVip
} from '../api/vip'
export default {
  data () {
    var checkTel = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      }
      setTimeout(() => {
        // 匹配13，14，15，16,17,18开头的手机号码！
        var reg = /^0?1[3|4|5|6|7|8][0-9]\d{8}$/
        if (!reg.test(value)) {
          callback(new Error('请输入正确的手机号码！'))
        } else {
          callback()
        }
      }, 1000)
    }
    return {
      ruleForm: {
        name: this.$store.state.admininfo.adminName,
        gender: '',
        phoneNum: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        gender: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        phoneNum: [
          { required: true, validator: checkTel, trigger: 'blur' }
        ],
      }
    }
  },

  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
    addVip(this.ruleForm).then((res)=>{
       if(res.data.status==0){
          alert("已存在该号码！")
      }
      if(res.data.status==1){
          alert("注册成功！")
           this.$refs[formName].resetFields()
      }
        
    })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>



